<template>
    <div class="home">
        <MountainList :mountains="mountains" />
    </div>
</template>

<script>
// @ is an alias to /src
import MountainList from '@/components/MountainList.vue'
import underscore from 'underscore';
export default {
    name: 'Home',
    components: {
        MountainList
},
data() {
    return {

    }
},
methods: {
    shuffleArray(array) {
        for (var i = array.length - 1; i > 0; i--) {
            var j = Math.floor(Math.random() * (i + 1));
            var temp = array[i];
            array[i] = array[j];
            array[j] = temp;
        }
    }
},
computed: {
    mountains() {
        return underscore.shuffle(this.$root.$data.mountains);
    }
},
}
</script>
